@media (max-width: 767px) {
    .container {
        width: 540px; }
    .nav-menu {
        display: none;
        position: fixed;
        top: 0;
        right: -100%;
        height: 100vh;
        background: #FF9C9C;
        z-index: 20;
        width: 50%;
        transition: 1s all;
        &__list {
            flex-direction: column;
            justify-content: space-evenly;
            align-items: center;
            height: 100%;
            margin-right: 0px; }
        &__list-item {
            margin-right: 0px; } }
    .hamburger {
        width: 30px;
        position: absolute;
        top: 40px;
        right: 30px;
        height: 14px;
        span {
            height: 3px;
            width: 100%;
            display: block;
            background: black;
            margin-bottom: 4px;
            transition: 1s all;
            &:last-child {
                margin-bottom: 0px; } } }
    .hamburger_active {
        z-index: 30;
        span {
            margin-bottom: -2px;
            &:nth-child(1) {
                rotate: (-45deg); }
            &:nth-child(2) {
                display: none; }
            &:nth-child(3) {
                rotate: (45deg); } } }
    .nav-menu_active {
        display: block;
        right: 0px;
        transition: 1s all; } }





@media (max-width: 575px) {
    .container {
        width: 100%; } }










